.active-users-container {
  padding: 24px;
  background-color: #e6f3ff;
  min-height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.active-users-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px;
}

.active-users-header h1 {
  margin: 0;
  color: #0f4c81;
  font-size: 32px;
  font-weight: 700;
}

.back-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 18px;
  border: 2px solid #3b82f6;
  background: transparent;
  border-radius: 10px;
  color: #1d4ed8;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-button:hover {
  background: #dbeafe;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.2);
}

.active-users-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}

.search-field {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 14px;
  background: white;
  border-radius: 12px;
  border: 1px solid #cbd5f5;
  min-width: 260px;
  box-shadow: 0 4px 12px rgba(15, 76, 129, 0.08);
}

.search-field input {
  border: none;
  outline: none;
  background: transparent;
  font-size: 14px;
  flex: 1;
  color: #0f4c81;
}

.search-field input::placeholder {
  color: #6b7280;
}

.search-icon {
  color: #60a5fa;
}

.table-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 12px 24px rgba(96, 165, 250, 0.18);
  overflow: hidden;
}

.active-users-table {
  width: 100%;
  border-collapse: collapse;
}

.active-users-table thead {
  background: linear-gradient(135deg, #60a5fa 0%, #3b82f6 100%);
  color: white;
}

.active-users-table th {
  padding: 16px 20px;
  text-align: left;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 0.6px;
  text-transform: uppercase;
}

.active-users-table tbody tr {
  background: white;
  transition: all 0.2s ease;
}

.active-users-table tbody tr:nth-child(even) {
  background: #f8fbff;
}

.active-users-table td {
  padding: 18px 20px;
  font-size: 14px;
  color: #0f4c81;
  border-bottom: 1px solid #e0ecff;
}

.active-users-table tbody tr:hover {
  background: #eef5ff;
  box-shadow: inset 0 0 0 9999px rgba(96, 165, 250, 0.08);
}

.details-column {
  text-align: right;
  width: 180px;
}

.details-button {
  padding: 10px 18px;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border: none;
  border-radius: 999px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 14px rgba(59, 130, 246, 0.3);
}

.details-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 20px rgba(59, 130, 246, 0.35);
}

.table-message {
  text-align: center;
  padding: 28px 16px;
  font-size: 14px;
  color: #4b5563;
}

.table-message.error {
  color: #dc2626;
}

@media (max-width: 992px) {
  .details-column {
    width: 160px;
  }

  .details-button {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .active-users-container {
    padding: 16px;
  }

  .active-users-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .active-users-header h1 {
    font-size: 26px;
  }

  .table-card {
    border-radius: 14px;
  }

  .details-column {
    width: 140px;
  }
}

@media (max-width: 600px) {
  .active-users-toolbar {
    flex-direction: column;
    align-items: stretch;
  }

  .search-field {
    width: 100%;
  }

  .active-users-table thead {
    display: none;
  }

  .active-users-table tr {
    display: grid;
    grid-template-columns: 1fr;
    gap: 12px;
    padding: 16px;
  }

  .active-users-table td {
    padding: 0;
    border: none;
    font-size: 13px;
  }

  .details-column {
    text-align: left;
  }
}
